.NavBar {
  display: flex;
  position: sticky;
  justify-content: space-between;
  /* Created with https://www.css-gradient.com */
  background-color: white;
  height: 5rem;
  top: 0px;
  align-items: center;
  width: 100vw;
  z-index: 2;
}

.NavBar__Logo
{
  overflow: hidden;
  cursor: pointer;
}

.NavBarLinkDiv {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  margin-right: 50px;
}


.NavBar_Search {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 0.7;
  height: 100%;
}

.NavBar_Search input {
  width: 80%;
  height: 30px;
  font-size: 20px;
  border: none;
  outline: none;
  background-color: transparent;
  border-bottom: 2px solid gray;
}

.NavBar_SearchPopulate {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  top: 70px;
  min-width: 80%;
  background-color: white;
  visibility: hidden;
}

.NavBar_Search input:valid ~ .NavBar_SearchPopulate {
  visibility: visible;
}

/* CSS */
.button66 {
  background-color: #0a6bff;
  border-radius: 4px;
  border: 0;
  box-shadow: rgba(1,60,136,.5) 0 -1px 3px 0 inset,rgba(0,44,97,.1) 0 3px 6px 0;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  display: inherit;
  font-family: "Space Grotesk",-apple-system,system-ui,"Segoe UI",Roboto,Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";
  font-size: 18px;
  font-weight: 700;
  line-height: 24px;
  margin: 0;
  min-height: 56px;
  min-width: 120px;
  padding: 16px 20px;
  position: relative;
  text-align: center;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  vertical-align: baseline;
  transition: all .2s cubic-bezier(.22, .61, .36, 1);
}

.button66:hover {
  background-color: #065dd8;
  transform: translateY(-2px);
}

@media (min-width: 768px) {
  .button66 {
    padding: 16px 44px;
    min-width: 150px;
  }
}

.NavBarLink
{
  margin: 1rem;
}