.LandingPage__Outer
{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
  width: 100vw;
  height: 90vh;
  background-color: rgb(246, 246, 246);
}

.Outer__Content1
{

  width: 40%;
  min-width: 30rem;
  aspect-ratio: 1.5/1;
}

.Outer__Content1 img{
  height: 100%;
  width: 100%;
}

.Outer__Content2
{
  display: flex;
  justify-content: center;
  width: 55%;
  height: 90%;
  min-width: 30rem;
  margin: 1rem;
}

.Outer__Content2 h1{
  display: flex;
  justify-content: center;
  width: 100%;
  font-family: 'Times New Roman', Times, serif;
  color: silver;
  font-size: 4rem;

}