.signupcard {
    background-color: white;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .signupcard .signupcardinput {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    overflow-y: scroll;
  }
  
  .signupcard .inputdata {
    position: relative;
    padding: 50px;
    width: 300px;
  }
  
  .signupcard .inputdata input {
    width: 100%;
    border: none;
    border-bottom: 2px solid silver;
    outline: none;
    font-size: 20px;
  }
  
  .signupcard .inputdata label {
    position: absolute;
    bottom: 55px;
    color: gray;
    pointer-events: none;
    transition: all 0.3s ease;
    font-size: 20px;
  }
  
  .signupcard .inputdata .underline {
    position: relative;
    background: red;
    height: 2px;
    width: 100%;
    bottom: 2px;
    transform: scaleX(0);
    transition: all 0.3s ease;
  }
  
  input[type="date"]:required:invalid {
    /* Chrome/Opera/Safari */
  
    color: transparent;
  }
  
  input[type="date"]:required:valid {
    /* Chrome/Opera/Safari */
  
    color: black !important;
  }
  
  input[type="date"]:required:focus {
    /* Chrome/Opera/Safari */
  
    color: grey !important;
  }
  
  input[type="date"]:required:focus {
    /* Firefox 19+ */
  
    color: black;
  }
  
  .form {
    display: flex;
    justify-content: center;
    height: 80%;
  }
  /*---------------------EFFECTS----------------------*/
  
  .signupcard .inputdata input:focus ~ label,
  .signupcard .inputdata input:valid ~ label {
    bottom: 80px;
  }
  
  .signupcard .inputdata input:focus ~ .underline,
  .signupcard .inputdata input:valid ~ .underline {
    transform: scaleX(1);
  }
  
  
  .button66 {
    background-color: #0a6bff;
    border-radius: 4px;
    border: 0;
    box-shadow: rgba(1,60,136,.5) 0 -1px 3px 0 inset,rgba(0,44,97,.1) 0 3px 6px 0;
    box-sizing: border-box;
    color: #fff;
    cursor: pointer;
    display: inherit;
    font-family: "Space Grotesk",-apple-system,system-ui,"Segoe UI",Roboto,Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";
    font-size: 18px;
    font-weight: 700;
    line-height: 24px;
    margin: 0.2rem;
    min-height: 56px;
    min-width: 120px;
    padding: 16px 20px;
    position: relative;
    text-align: center;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    vertical-align: baseline;
    transition: all .2s cubic-bezier(.22, .61, .36, 1);
  }
  
  .button66:hover {
    background-color: #065dd8;
    transform: translateY(-2px);
  }
  
  @media (min-width: 768px) {
    .button66 {
      padding: 16px 44px;
      min-width: 150px;
    }
  }
  
